  margin-right: auto;
}

.container-blocks.adaptiv {
  width: 100%;
  margin-top: 72px;
}

img {
  display: block;
}

.container {
  max-width: 335px;
  flex-direction: column;
  flex-shrink: 0;
  align-items: flex-start;
  gap: 32px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.title-container {
  height: 204px;
  flex-direction: column;
  flex-shrink: 0;
  align-items: center;
  row-gap: 24px;
  margin-bottom: 32px;
  display: flex;
}

.title {
  color: var(--black-main);
  text-align: center;
  letter-spacing: -.6px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}

.blocks-title-title {
  color: var(--orange-main);
  letter-spacing: -.6px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}

.description {
  color: var(--black-main);
  text-align: center;
  letter-spacing: -.28px;
  margin: 0;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.card-container {
  width: 335px;
  flex-direction: column;
  flex-shrink: 0;
  align-items: flex-start;
  gap: 20px;
  display: flex;
}

.card-taste {
  width: 100%;
  max-width: 335px;
  height: 270px;
  transform-style: preserve-3d;
  perspective: 800px;
  cursor: context-menu;
  margin-bottom: 20px;
  display: flex;
  position: relative;
}

.card-taste .front, .card-taste .back {
  backface-visibility: hidden;
  transform-style: preserve-3d;
  border-radius: 15px;
  flex-direction: column;
  padding: 28px;
  transition: transform .8s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.card-taste .milk {
  background-color: var(--white-main);
  border: 1px solid var(--orange-main);
}

.title-card {
  letter-spacing: -.28px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.milk .title-card {
  color: var(--black-main);
}

.milk-chocolate .title-card {
  color: var(--white-main);
}

.card-taste .milk-chocolate {
  background-color: var(--orange-main);
}

.card-taste .nuts {
  background-color: var(--dark-purple-main);
}

.card-taste .back {
  background-color: var(--white-main);
  transform: rotateY(180deg);
}

.card-taste:hover .front {
  transform: rotateY(-180deg);
}

.card-taste:hover .back {
  transform: rotateY(0);
}

.card-taste .contents {
  width: 100%;
  height: 100%;
  color: var(--white-main);
  transform: translateZ(100px)scale(.86);
}

.card-taste .front .img-taste {
  height: 171px;
  margin: 0 auto 25px;
}

.card-taste .contents .title-taste {
  letter-spacing: -.02em;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1.29;
}

.card-taste .contents .subtitle-taste {
  letter-spacing: -.03em;
  text-transform: uppercase;
  color: var(--black-main);
  margin-bottom: 8px;
  font-size: 18px;
  line-height: 1.22;
}

.card-taste .contents .text-taste {
  width: 279px;
  height: 146px;
  color: #111;
  text-overflow: ellipsis;
  letter-spacing: -.28px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  overflow: hidden;
}

.card-taste .contents .list-taste {
  align-items: center;
  gap: 6px;
  margin-bottom: 14px;
  display: flex;
}

.card-taste .contents .btn-taste {
  color: var(--black-main);
  border: 1px solid var(--orange-main);
  cursor: pointer;
  background: none;
  border-radius: 35px;
  padding: 6px 16px;
  font-size: 10px;
  line-height: 1.2;
  display: block;
}

.card-taste .contents .btn-taste.active-taste {
  color: var(--white-main);
  background: var(--orange-main);
  border: rgba(0, 0, 0, 0);
}

